.top-menu li {
  display: inline-block;
  position: relative;
  padding-bottom: 3px;
  &:after {
    content: '';
    display: block;
    position: absolute;
    right: -15px;
    bottom: 0;
    height: 6px;
    width: 0;
    background: transparent;
    transition: width .5s ease, background-color .5s ease; }
  &:hover:after {
    width: 100%;
    background: #f8af00; } }
